.table__filters {
  display: grid;
  align-items: flex-end;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 3rem;
  gap: 1rem;

  @media (width <= 1180px) {
    gap: 1rem;
  }

  @media (width >= 1024px) {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    gap: 3rem;
  }

  &_start {
    @media (width >= 1024px) {
      justify-content: flex-start;
    }
  }
}

.table__filter {
  &_search {
    @media (width >= 1180px) {
      min-width: 24.3rem;
    }
  }

  &_large {
    @media (width >= 1180px) {
      min-width: 36rem;
    }
  }

  &_status {
    @media (width >= 1180px) {
      min-width: 10.5rem;
    }
  }

  &_type {
    @media (width >= 1180px) {
      min-width: 12.4rem;
    }
  }

  &_succesful {
    @media (width >= 1180px) {
      min-width: 18.5rem;
    }
  }

  &_os {
    @media (width >= 1180px) {
      min-width: 12.4rem;
    }
  }

  &_geo {
    @media (width >= 1180px) {
      min-width: 16rem;
    }
  }
}

.table__filter-label {
  margin-bottom: 1rem;
  font-weight: 500;
}

.table__filter-icon {
  margin-right: 1rem;
  stroke: var(--yellow);
  transition: transform 0.3s ease;
}