html {
  box-sizing: border-box;
  font-size: 10px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 1.5;
}

.main {
  flex: 1;
  margin-bottom: 5rem;
}

.container {
  margin-right: auto;
  margin-left: auto;
  padding-right: 1.6rem;
  padding-left: 1.6rem;
  max-width: 117.2rem;
}

.page-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.text-center {
  text-align: center;
}

.big-title {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 700;
  font-size: 4rem;
}

.title {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 700;
  font-size: 3rem;
}

.small-title {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 700;
  font-size: 2rem;
}

.descr {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 1.6rem;
}

.text {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 1.4rem;
}

.small-text {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 1.2rem;
}

svg * {
  pointer-events: none;
}

.page-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 6rem;
  margin-bottom: 4rem;

  @media (width <= 480px) {
    flex-direction: column;
    margin-top: 3rem;
    margin-bottom: 3rem;
    gap: 2rem;
  }

  > * {
    margin-right: 1rem;
  }
}

.page-sub-head {
  margin-bottom: 2rem;
}

.hide {
  opacity: 0;
  pointer-events: none;
}

.main__wrapper {
  height: 100%;
}

.main__container {
  display: flex;
  flex-direction: column;
  height: 100%;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.scroll-hidden {
  overflow: hidden;
}

.table__outer {
  overflow-x: auto;
  margin-bottom: 2rem;

  @media (width <= 768px) {
    margin-right: -1.6rem;
    margin-left: -1.6rem;
    padding: 0 1.6rem;
  }
}

table {
  border-collapse: collapse;
  width: 100%;

  thead {
    td {
      position: relative;
      color: var(--gray-1);

      &::before {
        content: "";
        position: absolute;
        inset: 0;
        z-index: -1;
        border-top: 0.1rem solid var(--gray-2);
        background-color: var(--gray-3);
      }

      &:first-child {
        &::before {
          border-radius: 0.5rem 0 0;
        }
      }

      &:last-child {
        &::before {
          border-radius: 0 0.5rem 0 0;
        }
      }
    }
  }

  tbody {
    td {
      &:not(:has(img)) {
        @media (width <= 1180px) {
          white-space: nowrap;
        }
      }
    }

    tr {
      &:last-child {
        td {
          &:first-child {
            &::before {
              border-radius: 0 0 0 0.5rem;
            }
          }

          &:last-child {
            &::before {
              border-radius: 0 0 0.5rem;
            }
          }
        }
      }
    }
  }

  td {
    position: relative;
    line-height: 1;

    &:not(:has(button)) {
      padding: 0.3rem 1.2rem;

      @media (width >= 768px) {
        padding: 1.2rem;
      }
    }

    &:has(.td-buttons_small) {
      width: 9rem;
    }

    &::before {
      content: "";
      position: absolute;
      inset: 0;
      border-top: none;
      border-right: 0.1rem solid var(--gray-2);
      border-bottom: 0.1rem solid var(--gray-2);
      pointer-events: none;
    }

    &:first-child {
      &::before {
        border-left: 0.1rem solid var(--gray-2);
      }
    }

    img {
      &:not(:last-child) {
        margin-right: 0.5rem;
      }
    }
  }
}
