.admin-click-prices {
  .table__filter#format {
    min-width: 14rem;
  }
}

.admin-click-prices__price {
  display: flex;
  align-items: center;
  padding: 1.2rem;

  @media (width >= 768px) {
    min-width: 54.5rem;
  }
}

.admin-click-prices__edit {
  margin-left: 1rem;
  padding: 0;
}

.admin-click-prices__value {
  margin: 0;
  border: none;
  padding: 0;
  width: 3.6rem;
  min-width: auto;
  max-width: 100%;
  text-overflow:ellipsis;
  background-color: transparent;

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    display: none;
  }

  &:disabled {
    color: var(--black);
  }

  &:focus,
  &:focus-visible {
    outline: none;
    border: none;
    box-shadow: none;
  }
}
