.radio {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.radio__input {
  position: absolute;
  width: 0.1rem;
  height: 0.1rem;
  opacity: 0;
  pointer-events: none;

  &:focus,
  &:focus-visible {
    ~ .radio__oval {
      border: 0.1rem solid var(--black);
    }
  }

  &:checked {
    ~ .radio__oval {
      border: 0.1rem solid var(--yellow);

      &::before {
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
      }
    }
  }
}

.radio__oval {
  position: relative;
  margin-right: 1rem;
  border: 0.1rem solid var(--gray-2);
  border-radius: 100%;
  width: 2.4rem;
  height: 2.4rem;
  transition: border 0.3s ease;

  &::before {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 100%;
    width: 1.6rem;
    height: 1.6rem;
    background-color: var(--yellow);
    opacity: 0;
    transform: translate(-50%, -50%) scale(0);
    transition: opacity 0.3s ease, transform 0.3s ease;
  }
}
