.pagination__footer {
  display: flex;
  justify-content: flex-end;
  margin-top: auto;

  + .page-sub-head {
    margin-top: 4rem;
  }
}

.pagination__pages {
  display: flex;
  align-items: center;
  border: 0.1rem solid var(--gray-2);
  border-radius: 1.5rem;
  padding: 1.2rem 1rem;
}

.pagination__page {
  margin: 0 3.4rem;
  color: var(--gray-1);
}

.pagination__page-arrow {
  margin: 0;
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;

  @media (any-hover) {
    &:hover {
      .pagination__page-icon {
        fill: var(--gray-1);
      }
    }
  }

  &:active {
    .pagination__page-icon {
      fill: var(--black);
    }
  }
}

.pagination__page-next {
  transform: rotate(180deg);
}

.pagination__page-icon {
  display: block;
  fill: var(--gray-2);
  transition: fill 0.15s ease;
}
