.add-invoice-payments__title {
  margin-bottom: 3rem;
  text-align: center;
}

.add-invoice-payments__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  gap: 2rem;

  @media (width <= 480px) {
    grid-template-columns: 1fr;
  }
}

.add-invoice-payments__text {
  display: block;
  margin-bottom: 1rem;
}

.add-invoice-payments__input {
  width: 100%;
}

.add-invoice-payments__button {
  margin-top: 3rem;
  margin-right: auto;
  margin-left: auto;
  padding: 1.2rem;
  width: 100%;
  max-width: 20rem;
}
