.switcher {
  position: relative;
  display: flex;
  align-items: center;
  border-radius: 1rem;
  min-width: 11.5rem;
  background-color: var(--gray-3);

  // .switcher_short
  &_short {
    min-width: 7.6rem;

    .switcher__holder {
      width: 50%;
    }

    .switcher__button {
      &:nth-child(2) {
        &::before {
          content: none;
        }
      }

      &:has(.switcher__input:checked) {
        &:nth-child(2) {
          ~ .switcher__holder {
            background-color: var(--green);
            transform: translateX(100%);
          }
        }
      }
    }
  }

  // .switcher_statistics
  &_statistics {
    min-width: 14rem;

    .switcher__holder {
      width: 50%;
    }

    .switcher__button {
      &:nth-child(2) {
        &::before {
          content: none;
        }
      }

      &:has(.switcher__input:checked) {
        &:first-child {
          color: var(--black);

          ~ .switcher__holder {
            background-color: var(--green);
          }
        }

        &:nth-child(2) {
          color: var(--white);

          ~ .switcher__holder {
            background-color: var(--red);
            transform: translateX(100%);
          }
        }
      }
    }
  }
}

.switcher__button {
  position: relative;
  z-index: 1;
  margin: 0;
  border: none;
  padding: 0.4rem 0.5rem;
  width: 100%;
  font-size: 1.2rem;
  text-align: center;
  background-color: transparent;
  transition: color 0.3s ease;
  cursor: pointer;

  &:nth-child(2) {
    &::before {
      content: "";
      position: absolute;
      left: -0.1rem;
      top: 50%;
      z-index: 2;
      display: block;
      border-right: 0.1rem solid var(--gray-2);
      border-left: 0.1rem solid var(--gray-2);
      width: 106%;
      height: 75%;
      transform: translateY(-50%);
      pointer-events: none;
    }
  }

  &:has(.switcher__input:checked) {
    &:first-child {
      color: var(--white);

      ~ .switcher__holder {
        background-color: var(--red);
      }
    }

    &:nth-child(2) {
      ~ .switcher__holder {
        background-color: var(--yellow);
        transform: translateX(100%);
      }
    }

    &:nth-child(3) {
      ~ .switcher__holder {
        background-color: var(--green);
        transform: translateX(200%);
      }
    }
  }
}

.switcher__input {
  position: absolute;
  opacity: 0;
  pointer-events: none;
}

.switcher__holder {
  position: absolute;
  border-radius: 1rem;
  width: 33.33%;
  height: 100%;
  transition: transform 0.3s ease, background-color 0.3s ease;
}
