.modal {
  position: fixed;
  inset: 0;
  z-index: 10000;
  display: grid;
  place-items: center center;
  overflow: auto;
  opacity: 0;
  transition: opacity 0.15s ease;
  pointer-events: none;

  @media (width <= 480px) {
    align-items: end;
  }

  &.is-open {
    opacity: 1;
    pointer-events: all;

    .modal__holder {
      background-color: rgba(0, 0, 0, 0.2);
    }

    .modal__container {
      transform: translateY(0);
    }
  }
}

.modal__holder {
  position: absolute;
  inset: 0;
}

.modal__container {
  position: relative;
  z-index: 1;
  border-radius: 2rem;
  padding: 6rem 4rem;
  width: 100%;
  max-width: 55.5rem;
  background-color: var(--white);
  transform: scale(0);
  transform: translateY(5rem);
  transition: transform 0.3s ease;

  @media (width <= 480px) {
    border-radius: 2rem 2rem 0 0;
    padding: 2rem 1rem;
  }
}

.modal__close {
  position: absolute;
  right: 2rem;
  top: 2rem;
  border: 0.1rem solid var(--gray-2);
  border-radius: 100%;
  width: 4.8rem;
  height: 4.8rem;
  background-color: transparent;
  transition: transform 0.3s ease;
  cursor: pointer;

  @media (width <= 480px) {
    position: sticky;
    left: auto;
    right: 0;
    display: block;
    margin-left: auto;
    background-color: var(--white);
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 4rem;
    width: 50%;
    height: 0.2rem;
    background-color: var(--black);
  }

  &::before {
    transform: translate(-50%, -50%) rotate(45deg);
  }

  &::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }

  &:hover {
    transform: rotate(90deg) scale(1.05);
  }

  &:active {
    transform: rotate(90deg) scale(0.98);
  }
}