.statistics__total {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1.5rem;
}

.statistics__total-title  {
  margin-bottom: 0.8rem;
}

.statistics__total-text {
  display: flex;
  align-items: center;
  margin-top: 1rem;
  font-weight: 600;
}

.statistics__total-balance {
  margin-left: 1rem;
  border-radius: 1rem;
  padding: 0.4rem 1.5rem;
  font-weight: 500;
  background-color: var(--green);
}
