.edit-modal__box {
  margin-right: auto;
  margin-left: auto;
  max-width: 30rem;
}

.edit-modal__title {
  margin-bottom: 3rem;
  text-align: center;
}

.edit-modal__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.edit-modal__item {
  & + & {
    margin-top: 2rem;
  }
}

.edit-modal__text {
  display: block;
  margin-bottom: 1rem;
}

.edit-modal__input {
  width: 100%;
}

.edit-modal__button {
  margin-top: 3rem;
  margin-right: auto;
  margin-left: auto;
  padding: 1.2rem;
  width: 100%;
  max-width: 100%;
}
