.td-buttons {
  display: flex;
  align-items: center;
  height: 100%;

  * + * {
    position: relative;

    &::before,
    &::after{
      content: '';
      position: absolute;
      inset: 0;
      border-left: 1px solid var(--gray-2);
    }
  }
}

.button-stat,
.button-edit,
.button-download,
.button-delete {
  border: none;
  background-color: transparent;
  cursor: pointer;

  &:hover {
    .button-stat__icon,
    .button-edit__icon,
    .button-download__icon,
    .button-delete__icon {
      fill: var(--black);
      transform: scale(1.1);
    }
  }

  &:active {
    .button-stat__icon,
    .button-edit__icon,
    .button-download__icon,
    .button-delete__icon {
      transform: scale(1.2);
    }
  }

  * {
    pointer-events: none;
  }

  &__icon {
    display: block;
    fill: var(--gray-1);
    transition: fill 0.3s ease, transform 0.3s ease;
  }
}

.button-stat,
.button-edit,
.button-delete {
  padding: 1.1rem;
}

.button-delete {
  display: flex;
  justify-content: center;
  width: 100%;
}
