.number {
  position: relative;
  display: block;

  &:hover {
    input[type="number"] {
      border: 0.1rem solid var(--black);
    }
  }
}

.number input[type="number"] {
  border: 0.1rem solid var(--gray-2);
  border-radius: 1.6rem;
  padding: 1.2rem;
  width: 100%;
  font-weight: 400;
  font-size: 1.4rem;
  color: var(--black);
  transition: all 0.3s ease;

  &::placeholder {
    font-family: inherit;
    font-weight: inherit;
    font-size: inherit;
    color: var(--gray-1);
  }

  &:focus,
  &:active,
  &:focus-visible {
    border: 0.1rem solid var(--black);
  }
}

.number input[type="number"]::-webkit-outer-spin-button,
.number input[type="number"]::-webkit-inner-spin-button {
  display: none;
}

.number-minus,
.number-plus {
  position: absolute;
  right: 1.2rem;
  margin: 0;
  border: none;
  padding: 0;
  width: 1.6rem;
  height: 1.6rem;
  background-position: center;
  background-size: 1.4rem 0.9rem;
  background-repeat: no-repeat;
  background-color: transparent;
  opacity: 0.4;
  transition: opacity 0.3s ease;
  cursor: pointer;
}

.number-minus {
  bottom: 0.6rem;
  background-image: url("../img/icons/arrow-down.svg");

  &:hover {
    opacity: 1;
  }
}

.number-plus {
  top: 0.6rem;
  background-image: url("../img/icons/arrow-up.svg");

  &:hover {
    opacity: 1;
  }
}
