.add-modal__button {
  margin-top: 2rem;
  margin-right: auto;
  margin-left: auto;
  padding: 1.2rem;
  width: 100%;
  max-width: 20rem;
}

.add-modal__label {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.add-modal__title {
  margin-bottom: 3rem;
  text-align: center;
}

.add-modal__li {
  &.hide {
    display: none;
  }

  &_full {
    grid-column: 2 span;
  }
}

.add-modal__inputs {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  list-style: none;
  margin: 0;
  margin-bottom: 3rem;
  padding: 0;
  gap: 2rem 3rem;

  @media (width <= 480px) {
    gap: 1rem;
  }
}

.add-modal__text {
  display: block;
  padding-bottom: 1rem;
  font-weight: 500;
}

.add-modal__radios {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.add-modal__footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3rem;
}

.add-modal__footer-checkbox {
  display: flex;
  flex: 1;
  align-items: center;
}
