.header {
  padding: 3rem 0;

  @media (width <= 1023px) {
    padding: 1rem 0;
  }
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media (width <= 480px) {
    flex-direction: column;
    gap: 1rem;
  }
}

.header__logo {
  display: block;

  @media (width <= 480px) {
    max-width: 9rem;
  }
}

.header__nav {
  position: relative;

  @media (width <= 1023px) {
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 5000;
    padding: 1rem;
    width: 100%;
  }

  @media (width <= 480px) {
    padding: 0.5rem;
  }

  &::before {
    @media (width <= 1023px) {
      content: "";
    }

    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    display: block;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    pointer-events: none;
    backdrop-filter: blur(40px);
  }
}

.header__list {
  display: flex;
  align-items: center;
  list-style-type: none;
  margin: 0;
  padding: 0;

  @media (width <= 1023px) {
    justify-content: center;
    overflow-x: auto;
    margin-right: auto;
    margin-left: auto;
    gap: 1rem;
  }

  @media (width <= 480px) {
    justify-content: flex-start;
    margin-right: -0.5rem;
    margin-left: -0.5rem;
    padding: 0 0.5rem;
    gap: 0.5rem;
  }
}

.header__item {
  @media (width <= 1023px) {
    width: 100%;
  }

  & + & {
    @media (width >= 1024px) {
      margin-left: 3rem;
    }
  }
}

.header__link {
  display: flex;
  align-items: center;
  padding: 1.2rem 0;
  font-weight: 600;
  text-decoration: none;
  color: var(--gray-1);
  transition: color 0.3s ease;

  @media (width <= 1023px) {
    display: block;
    padding: 0.5rem;
    width: 100%;
    white-space: nowrap;
    text-align: center;
  }

  &_active {
    color: var(--black);

    .header__icon {
      fill: var(--black);
    }

    @media (width <= 1023px) {
      color: var(--yellow);

      .header__icon {
        fill: var(--yellow);
      }
    }
  }

  @media (any-hover) {
    &:hover {
      color: var(--black);

      .header__icon {
        fill: var(--black);
      }
    }
  }
}

.header__icon {
  display: block;
  margin-right: 1rem;
  fill: var(--gray-1);
  transition: fill 0.3s ease;

  @media (width <= 1023px) {
    margin-right: auto;
    margin-bottom: 0.2rem;
    margin-left: auto;
    width: 1.6rem;
    height: 1.6rem;
  }
}

.header__info {
  display: flex;
  align-items: center;

  @media (width <= 480px) {
    width: 100%;
  }
}

.header__balance {
  display: flex;
  align-items: center;
  margin-right: 6rem;
  margin-left: 1rem;
  font-weight: 600;

  @media (width <= 1180px) {
    margin-right: 2rem;
  }

  @media (width <= 480px) {
    margin-right: 1rem;
    margin-left: 0;
  }
}

.header__currency {
  margin-right: 1rem;
}

.header__user {
  display: flex;
  align-items: center;
  margin-right: 3rem;

  @media (width <= 480px) {
    margin-right: 1rem;
  }

  &-icon {
    margin-right: 1rem;
  }
}

.header__name {
  margin-bottom: 0.2rem;
  font-weight: 600;
}

.header__mail {
  text-decoration: none;
  color: var(--gray-1);
  transition: color 0.3s ease;

}

.header__logout {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  border: 0.1rem solid rgba(0, 0, 0, 0.2);
  border-radius: 100%;
  padding: 1.2rem;
  background: none;
  transition: transform 0.15s ease, border 0.15s ease;
  cursor: pointer;

  @media (width <= 480px) {
    margin-left: auto;
  }

  @media (any-hover) {
    &:hover {
      border: 0.1rem solid rgba(0, 0, 0, 0.5);
      transform: scale(1.1);

      .header__logout-icon {
        fill: rgba(0, 0, 0, 0.5);
      }
    }
  }

  &:active {
    transform: scale(0.98);
  }

  &-icon {
    fill: var(--gray-1);
    transition: fill 0.15s ease;
  }
}
