.admin-invoices__table {
  thead {
    td {
      color: var(--gray-1);
    }
  }

  td {
    width: 23.9%;

    &:first-child {
      width: 7rem;
    }
  }
}

.admin-invoices__td-status {
  display: inline-block;
  border-radius: 1rem;
  padding: 0.4rem 1.5rem;
  width: 100%;
  max-width: 7.5rem;
  font-weight: 500;
  font-size: 1.2rem;
  text-align: center;

  &_wait {
    background-color: var(--yellow);
  }

  &_paid {
    background-color: var(--green);
  }
}

.admin-invoice__checkbox {
  justify-content: center;
}