.input {
  border: 0.1rem solid var(--gray-2);
  border-radius: 1.6rem;
  padding: 1.2rem;
  width: 100%;
  min-width: auto;
  max-width: 100%;
  font-weight: 400;
  font-size: 1.4rem;
  color: var(--black);
  transition: all 0.3s ease;

  &_search {
    padding-left: 3.8rem;
    background-image: url("../img/icons/input-search.svg");
    background-position: 1.2rem 50%;
    background-repeat: no-repeat;
  }
}

.input:hover,
.input:active,
.input:focus,
.input:focus-visible {
  border: 0.1rem solid var(--black);
}

.input::placeholder {
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  color: var(--gray-1);
}
