.treeselect {
  width: auto;
  min-width: 10rem;

  .treeselect-input {
    position: relative;
    z-index: 1001;
    border: none;
    padding: 1.2rem;
    min-width: auto;
    max-width: 100%;
    background: transparent;

    &:hover {
      &::before {
        border: 0.1rem solid var(--black);
      }
    }

    &::before {
      content: "";
      position: absolute;
      inset: 0;
      border: 0.1rem solid var(--gray-2);
      border-radius: 1.5rem;
      width: 100%;
      height: 100%;
      transition: border 0.3s ease;
      pointer-events: none;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      border-right: 0.1rem solid var(--black);
      border-left: 0.1rem solid var(--black);
      width: 100%;
      height: 100%;
      opacity: 0;
      pointer-events: none;
    }
  }

  .treeselect-input--opened {
    &::before {
      border: 0.1rem solid var(--black);
    }

    &::after {
      opacity: 1;
    }
  }

  .treeselect-input__operators {
    margin-right: 1.2rem;
  }

  .treeselect-input__edit {
    line-height: 1.5;
  }

  .treeselect-list {
    z-index: 1002;
    overflow-y: auto;
    margin-right: 0.1rem;
    border-top: none;
    border-right: 0.1rem solid var(--black);
    border-bottom: 0.1rem solid var(--black);
    border-left: 0.1rem solid var(--black);
    border-radius: 0 0 1.5rem 1.5rem;
    max-height: 22rem;

    &::-webkit-scrollbar {
      width: 0.5rem;
    }

    &::-webkit-scrollbar-track {
      background-color: transparent;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 4rem;
      background-color: var(--gray-2);
    }
  }

  .treeselect-list__item {
    padding: 0.2rem 1.2rem;
    height: auto;
    font-size: 1.4rem;
    line-height: 1.5;
    color: var(--gray-1);

    &--focused {
      background-color: transparent;

      .treeselect-list__item-label {
        color: var(--black);
      }
    }
  }

  .treeselect-list.treeselect-list--single-select
    .treeselect-list__item--single-selected {
    color: var(--gray-1);
    background-color: transparent;
  }

  .treeselect-list__item--checked {
    .treeselect-list__item-label {
      &::after {
        opacity: 1;
        transform: translateY(-50%) scale(1);
      }
    }
  }

  .treeselect-list__item-label {
    position: relative;
    padding-right: 1rem;

    &::after {
      content: "";
      position: absolute;
      right: 0;
      top: 50%;
      width: 1rem;
      height: 1.2rem;
      background-image: url("../img/icons/check.svg");
      opacity: 0;
      transform: translateY(-50%) scale(0);
      transition: transform 0.3s ease;
      pointer-events: none;
    }
  }

  .treeselect-input__tags {
    padding-right: 3rem;
  }

  .treeselect-input__tags-element {
    flex-shrink: 0;
    padding: 0;
    font-size: 1.4rem;
    background-color: transparent;
    pointer-events: none;

    .treeselect-input__tags-cross {
      display: none;
    }
  }
}
