.tabs {
  display: flex;
  margin-bottom: 3rem;
  gap: 3rem;

  @media (width <= 768px) {
    flex-wrap: wrap;
    gap: 1rem;
  }
}

.tab {
  @media (width >= 767px) {
    width: 100%;
  }
}

.tab__button {
  border: 0.1rem solid var(--gray-2);
  border-radius: 1.5rem;
  padding: 0.5rem 1rem;
  font-weight: 600;
  font-size: 1.2rem;
  color: var(--gray-1);
  background-color: transparent;
  transition: border 0.3s ease, color 0.3s ease, background-color 0.3s ease;
  cursor: pointer;

  @media (width >= 767px) {
    padding: 1.2rem;
    width: 100%;
    font-size: 1.6rem;
  }

  &_active {
    color: var(--black);
    background-color: var(--yellow);
  }

  &:not(.tab__button_active) {
    &:hover {
      border: 0.1rem solid var(--yellow);
      color: var(--yellow);
    }
  }
}
