.auth {
  position: relative;
  display: flex;
  justify-content: center;
  width: 100%;

  @media (width <= 480px) {
    padding-right: 1rem;
    padding-left: 1rem;
    background-attachment: fixed;
  }

  &::before {
    content: '';
    position: fixed;
    inset: 0;
    z-index: -1;
    background-image: url("../img/pages/auth/auth-bg.svg");
    background-position: top;
    background-size: cover;
    background-repeat: no-repeat;
    pointer-events: none;

    @media (height >= 950px) {
      background-position: center;
    }
  }
}

.auth__container {
  margin-right: auto;
  margin-left: auto;
  max-width: 55.5rem;
}

.auth__logo {
  padding-top: 4rem;
  padding-bottom: 7.2rem;

  @media (width <= 480px) {
    padding: 3rem 0;
  }
}

.auth__form {
  border-radius: 5rem;
  padding: 6rem 12.8rem;
  width: 100%;
  max-width: 55.5rem;
  box-shadow: var(--default-shadow);
  background-color: var(--white);

  @media (width <= 480px) {
    padding: 2rem;
  }
}

.auth__title {
  margin-bottom: 4rem;
}

.auth__input {
  padding: 1.8rem 1.6rem;
  width: 100%;
  max-width: 30rem;
  font-size: 1.6rem;
}

.auth__input + .auth__input {
  margin-top: 3rem;
}

.auth__button {
  margin-top: 3rem;
  padding: 1.6rem;
  width: 100%;
}

.auth__bottom {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 3rem;
}

.auth__bottom-link {
  margin-left: 2rem;
  text-decoration: underline;
  color: var(--black);
}

.auth__bottom-link:hover {
  text-decoration: none;
}
