.user__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  list-style-type: none;
  margin: 0;
  padding: 0;
  gap: 2.4rem;

  @media (width <= 800px) {
    grid-template-columns: 1fr 1fr;
  }

  @media (width <= 540px) {
    grid-template-columns: 1fr;
  }
}

.user__card {
  border-radius: 2rem;
  transition: box-shadow 0.3s ease;

  &_megaphone {
    grid-column: 2 span;
    background-image: url("../img/icons/megaphone.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-color: var(--gray-2);

    @media (width <= 800px) {
      grid-column: 1 span;
    }

    @media (any-hover) {
      &:hover {
        box-shadow: 0 0 0 0.3rem var(--gray-2);
      }
    }
  }

  &_headphone {
    grid-row: 2 span;
    background-image: url("../img/icons/headphone.svg");
    background-position: 50% 90%;
    background-repeat: no-repeat;
    background-color: var(--blue);

    @media (width <= 800px) {
      grid-row: 1 span;
    }

    @media (any-hover) {
      &:hover {
        box-shadow: 0 0 0 0.3rem var(--blue);
      }
    }
  }

  &_graph {
    background-image: url("../img/icons/graph.svg");
    background-position: 50% 10.5rem;
    background-repeat: no-repeat;
    background-color: var(--yellow);

    @media (any-hover) {
      &:hover {
        box-shadow: 0 0 0 0.3rem var(--yellow);
      }
    }
  }

  &_wallet {
    background-image: url("../img/icons/wallet.svg");
    background-position: 50% 10.5rem;
    background-repeat: no-repeat;
    background-color: var(--green);

    @media (any-hover) {
      &:hover {
        box-shadow: 0 0 0 0.3rem var(--green);
      }
    }
  }

  @media (width <= 600px) {
    background-position: center;
    background-size: 50% 50%;
  }
}

.user__link {
  display: block;
  padding: 4rem 3rem;
  width: 100%;
  height: 100%;
  min-height: 28.5rem;
  font-weight: 700;
  text-decoration: none;
  color: var(--black);

  @media (width <= 600px) {
    min-height: auto;
  }
}

.user__wrap {
  display: flex;
  justify-content: space-between;
}

.user__text {
  margin-right: 2rem;
}

.user__more {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0.1rem solid rgba(36, 36, 36, 0.2);
  border-radius: 100%;
  padding: 1.2rem;
  transition: border 0.3s ease, transform 0.3s ease;

  @media (any-hover) {
    &:hover {
      border: 0.1rem solid var(--black);
      transform: scale(1.05);
    }
  }

  &:active {
    transform: scale(0.98);
  }
}
