.footer {
  margin-top: auto;
  padding: 2rem 0 1rem;
  text-align: center;
  color: var(--white);
  background-color: var(--black);

  @media (width <= 1023px) {
    padding-bottom: 7rem;
  }
}

.footer__links {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  margin: 0;
  margin-bottom: 1.3rem;
  padding: 0;
  gap: 3rem;
}

.footer__link {
  font-size: 1.2rem;
  color: inherit;
}

.footer__terms {
  font-size: 1rem;
}
